import React, { Component, PropTypes } from 'react';

/**
 * Header
 */
export class Header extends Component {
  scrollTo(e){
    console.log(e);
    // $('body').scrollTo('#'+e);
  }

  render() {
    return (
      <div>
        <nav id="navbar" className="navbar fixed-top navbar-expand-lg navbar-dark bg-dark">
          {/* <a className="navbar-brand" href="#">Navbar</a> */}
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto mx-auto">
              <li className="nav-item active" onClick={this.scrollTo("aboutSection")}>
                <span className="nav-link" href="/">About<span className="sr-only">(current)</span></span>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="/">Projects</a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="/">Contact</a>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    );
  }
}

// Header.propTypes = {
//   prop: PropTypes.type.isRequired
// }

export default Header;
